.solutions {
  color: $white;
  &__separator {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $main-color;
    padding: 10px $main-padding;
    height: 150px;
    text-align: center;
    background-image: linear-gradient(
      to right,
      #072042,
      #082449,
      #0a2750,
      #0d2b58,
      #0f2f5f,
      #0a3665,
      #043d6b,
      #004470,
      #004e72,
      #00586f,
      #00606a,
      #136763
    );
  }
  &__button {
    @include button-white;
    padding: 15px;
    height: fit-content;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-height: 40vh;
    padding: 10px $main-padding;
    background-image: linear-gradient(
        $transparent-main-color,
        $transparent-main-color
      ),
      url("../../assets/images/pin2.webp");
    background-size: cover;
    text-align: center;
  }
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 3rem;
  }
  &__title {
    color: $white;
    text-transform: capitalize;
    font-size: 2.5rem;
  }
  &__subtitle {
    color: $white;
    max-width: 600px;
    font-weight: 600;
  }
  &__text {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin: 30px 0;
  }
}
